import axios from '@/utils/uniAxios.js'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import {uniSearchBar} from "@/components/uni-search-bar/uni-search-bar.vue";
export default {
	components: {
		uniLoadMore, uniSearchBar
	},
	data() {
		return {
			historyGoods: ['窝窝头', '大闸蟹', '红烧肉', '西班牙大龙虾', '热干面', '牛肉面', '炸酱面'],
			cateMaskState: 0, //分类面板展开状态
			loadingType: 'more', //加载更多状态
			filterIndex: null, 
			productRequest: {
				productCategoryId: '',
				pageSize: 10,
				pageNum: 1,
				orderByType: ''
			}, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			listCategory: [],
			goodsList: [],
			isCross: false,
			isErr: false
		};
	},
	
	onLoad(options){



		this.productRequest.productCategoryId = +options.tid;
		// if (options.tid)
		this.listWithChildren();
		this.loadData('refresh');
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.goodsList = [];
		this.loadingType = 'more';
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods: {
		// 加载分类
		listWithChildren() {
			axios.post('home/list/withChildren', {}).then(({data}) => {
				this.listCategory = data.data.reduce((res, item) => {
					res = res.concat(item.productTypeList.reduce((r, val) => {
						r = r.concat(val.children);
						return r;
					}, []));
					return res;
				}, [])
			})
		},
		// 搜索商品列表
		async search(e) {
			this.isErr = false;
			this.productRequest.keywords = e ? e.value : '';
			let request = Object.assign({}, this.productRequest);
			for (let key in request) {
				if (!request[key]) delete request[key];
			}
			axios.post('/product/search', request).then(({data}) => {
				let goodsList = data.code === 200 ? data.data.list : [];
				this.goodsList = this.goodsList.concat(goodsList);
				//判断是否还有下一页，有是more  没有是nomore
				this.loadingType  = this.goodsList.length >= data.data.total - data.data.pageNum ? 'nomore' : 'more';
				uni.hideLoading();
			}).catch(() => {
				this.isErr = true;
				uni.hideLoading();
			})
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add') {
			if (this.loadingType === 'nomore') return;
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			});
			uni.showLoading({
				title: '正在加载'
			});
			//没有更多直接返回
			if (type === 'add') {
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			} else {
				this.loadingType = 'more'
			}
			if (type === 'refresh') {
				this.goodsList = [];
			}
			this.search();
			if (type === 'refresh') {
				uni.stopPullDownRefresh();
			}
		},
		//筛选点击
		tabClick(index){
			if (this.filterIndex === index) {
				this.filterIndex = null;
				return;
			}
			this.filterIndex = index;
		},
		tabItemClick(value) {
			this.filterIndex = null;
			this.productRequest.orderByType = value;
			this.loadData('refresh');	
		},
		tabICateClick(value) {
			this.filterIndex = null;
			this.productRequest.productCategoryId = value;
			this.loadData('refresh');	
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${item.id}`
			})
		},
		gotoIndex() {
			uni.switchTab({
				url: '/pages/index/index'
			})
		}
	},
}
